"use client"
import TureList from "@/components/admin/TureList";
import Container from "@/components/Container";
import Title from "@/components/ui/Title";
import React from "react";
import { useQuill } from "react-quilljs";

const page = () => {
  const { quill, quillRef } = useQuill();

  return (
    <main>
      <Title className="text-4xl font-bold" text="TURE" />
      <Title text="Her kan du redigere/slette eller lave nye ture" />

      <Container>
        <section className="w-full">
          <Title text="Lav ny tur" />
          <form>
            <input type="text" name="title" />
            <input type="text" name="traveltime" />
            <input type="text" name="destination" />
            <input type="text" name="distance" />
            <input type="text" name="price" />
            <input type="image" name="image1" />
            <input type="image" name="image2" />
            <input type="number" name="rating" max={5} min={0} />
            <input type="date" name="spacelaunch" />
            <div  ref={quillRef} />
          </form>
        </section>
        <TureList />
      </Container>
    </main>
  );
};

export default page;
